<template>
  <div class="loading-page" v-show="loadingShow" transition>loading</div>
  <div class="alert-page" v-if="alertShow" transition>
    <div class="alert-inner">
      <div>
      <i class="iconfont" :class="{'icon-warn': alertType == 'warn','icon-ok': alertType == 'ok','icon-jishi': alertType == 'waiting'}"></i>
      <p class="alert-txt" v-text="alertTxt"></p>
      </div>
    </div>
  </div>

  <!-- confirm -->
  <div class="v-dialog" v-show="dialogShow" transition="modal">
    <div class="ui-mask" @click="quit()"></div>
    <div class="dialog-box" id="dialogBox">
        <h3 class="dialog-title">
            提示信息
        </h3>
        <div class="dialog-content">
            <p v-text="dialogContent"></p>
        </div>
        <div class="dialog-bot">
            <a href="javascript:;" class="dialog-btn default" @click="dispatch('oncancel')">取消</a>
            <a href="javascript:;" class="dialog-btn primary" @click="dispatch('onconfirm')">确定</a>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['loadingShow', 'dialogShow', 'dialogContent', 'alertShow', 'alertType', 'alertTxt'],
  methods: {
    dispatch (eventStr) {
      this.$dispatch(eventStr)
    },
    quit () {
      this.$dispatch('quitConfirm')
    }
  }
}
</script>
<style lang="stylus">
@import '../assets/styl/index'
.center
  display flex
  align-items center
  justify-content center
.ui-mask
  position fixed
  left 0
  top 0
  bottom 0
  right 0
  z-index 1000
  background-color rgba(0, 0, 0, .6)
.loading-page
  position fixed
  width 100px
  height 100px
  background-color rgba(0, 0, 0, .7)
  top 20%
  left 50%
  margin-left -(@width / 2)
  border-radius 3px
  @extend .center
  color #fff
.alert-inner
  @extend .loading-page
  text-align center
  line-height 1.3
  i
    font-size 26px
  p
    margin 0
.modal-transition
  opacity: 1
  transition: all .3s ease
.modal-enter, .modal-leave
  opacity: 0
  .dialog-box
    transform: scale(1.1)
.v-dialog
  @extend .ui-mask
  @extend .center
  
.dialog-box
  transition all .2s linear
  width 80%;
  margin-top -50px
  background-color #fff
  text-align center
  border-radius 2px
  z-index 11000
  h3.dialog-title
    font-weight 400
    font-size: 17px
    color #333
  .dialog-content
    color #888
    padding: 0 15px
  .dialog-bot
    @extend .ui-border-t
    display flex
    a
      flex 1
      line-height 38px
      &.default
        @extend .ui-border-r
        color #888
.iconfont.icon-jishi
   animation loadings 1s linear infinite
   display inline-block
@keyframes loadings
  0% 
    transform rotate(0deg)
  100% 
    transform rotate(360deg)
</style>



