<template>
  <head-bar page-title="登录" back-bar="true" page-link="back"></head-bar>
  <div class="section-block">
    <ul class="section-list">
      <li class="cell"><input type="text" name="name" value="" placeholder="username"></li>
      <li class="cell"><input type="password" name="name" value="" placeholder="password"></li>
    </ul>
  </div>
  <a @click="login" class="login-btn">登录</a>
  <div class="ta-r">
      注册
  </div>
  <nav-bar></nav-bar>
</template>
<script>
export default {
  data () {
    let self = this
    return {
      alert: {
        txt: '',
        show: false,
        hideFn: function (ts) {
          let timer = ts || 2000
          setTimeout(function () {
            self.alert.show = false
          }, timer)
        }
      }
    }
  },
  components: {
    'navBar': require('../components/navBar.vue'),
    'headBar': require('../components/header.vue')
  },
  methods: {
    login () {
      let self = this
      window.localStorage.loginStatus = true
      let redirect = decodeURIComponent(self.$route.query.redirect || '/')
      self.alert.txt = '登录成功'
      self.alert.show = true
      setTimeout(function () {
        self.alert.hideFn()
        self.$route.router.go(redirect)
      }, 2000)
    }
  }
}

</script>
<style lang="stylus">
@import '../assets/styl/index'
.login-btn
  padding: px2rem(20) 0
  text-align center
  color: #fff;
  background-color themes-color
  border:0
  display block
  margin px2rem(20px)
  border-radius 2px
.ta-r
  text-align right
</style>



