<template>
  <head-bar page-title="购物车"></head-bar>
  <div class="flex-center ui-alert" v-if="!loginFlag" v-link="{path: '/login'}">
    <div class="ui-alert-icon">
      <i class="iconfont">&#xe60b;</i>
    </div>
    <div class="ui-alert-txt">
      请登录
    </div>
  </div>
  <datetime :readonly="true" format="YYYY-MM-DD"></datetime> <br>
  <datetime :readonly="true" format="MMM/D/YYYY" width="300px"></datetime>
  <nav-bar></nav-bar>
</template>
<script>
export default {
  data () {
    return {
      loginFlag: null
    }
  },
  route: {
    data (transition) {
      let self = this
      let loginStatus = window.localStorage.loginStatus
      if (loginStatus) {
        self.loginFlag = true
      } else {
        self.loginFlag = false
      }
    }
  },
  components: {
    'navBar': require('../components/navBar.vue'),
    'headBar': require('../components/header.vue'),
    'datetime': require('../components/datetime.vue')
  }

}

</script>
<style lang="stylus">
@import '../assets/styl/index'
#app
  height: 100%;
.flex-center
  align-items: center
  justify-content center
  height: 70%
.ui-alert-icon,.ui-alert-txt
  width 100%
.ui-alert
  text-align center
  .iconfont
    font-dpr(40px)
    display block
</style>



