export default function (router) {
  router.map({
    '/': {
      name: 'home',
      component: require('./App.vue')
    },
    'list': {
      name: 'list',
      component: require('./views/list.vue')
    },
    'cart': {
      name: 'cart',
      component: require('./views/cart.vue')
    },
    'login': {
      name: 'login',
      component: require('./views/login.vue')
    },
    'user': {
      name: 'user',
      auth: true,
      component: require('./views/user.vue')
      // component: function (resolve) {
      //   // 异步加载用户中心模块
      //   require(['./views/user.vue'], resolve)
      //   require(['./views/user-balance.vue'], resolve)
      //   require(['./views/user-coupon.vue'], resolve)
      //   require(['./views/user-collect.vue'], resolve)
      //   require(['./views/user-history.vue'], resolve)
      //   require(['./views/user-order.vue'], resolve)
      // }
    },
    'balance': {
      name: 'balance',
      auth: true,
      component: require('./views/user-balance.vue')
    },
    'collect': {
      name: 'collect',
      auth: true,
      component: require('./views/user-collect.vue')
    },
    'coupon': {
      name: 'coupon',
      auth: true,
      component: require('./views/user-coupon.vue')
    },
    'history': {
      name: 'history',
      auth: true,
      component: require('./views/user-history.vue')
    },
    'order': {
      name: 'order',
      auth: true,
      component: require('./views/user-order.vue')
    }

  })
}



