<template>
  <header id="header">
      <div class="top-icon-left" v-if="backBar" @click="backpages(pageLink)" >
      </div>
      <h1 v-text="pageTitle"></h1>
  </header>
</template>

<script>
export default {
  props: ['pageTitle', 'backBar', 'pageLink'],
  data () {
    return {
      msg: 'Hello World!'
    }
  },
  methods: {
    backpages (link) {
      let self = this
      if (link === 'back') {
        window.history.go(-1)
      } else {
        self.$route.router.go('/' + link)
      }
    }
  }
}
</script>
<style lang="stylus">
@import '../assets/styl/index'
#header
  background-color: themes-color
  text-align: center
  height: px2rem(100)
  line-height: px2rem(100)
  color:#fff
  position:relative
  h1
    margin: 0
    font-size 1.4em
    font-weight 400
.top-icon-left
  position:absolute
  top:0
  right: 0
.top-icon-left
  right: auto
  left: 0
  height: 100%
  width: px2rem(90)
  &:before
    display: block
    content: ''
    position:absolute
    width: px2rem(20)
    height: px2rem(20)
    border-top:2px solid #fff
    border-left:2px solid #fff
    top:50%
    left:50%
    transform: translate(-50%,-50%) rotate(-45deg)
</style>



