<template>
  <head-bar page-title="我的订单" back-bar="true" page-link="back"></head-bar>
  <div class="section-order-title" :class="{'cur1': curClass =='tag1','cur2': curClass =='tag2','cur3': curClass =='tag3','cur4': curClass =='tag4'}">
    <div class="order-bar flex-wrap">
        <span class="flex" @click="orderTab('1')">
            全部订单
        </span>
        <span class="flex" @click="orderTab('2')">
            待付款
        </span>
        <span class="flex" @click="orderTab('3')">
            待收货
        </span>
        <span class="flex" @click="orderTab('4')">
            待评价
        </span>
    </div>
    <span class="order-cur"></span>
  </div>
  <h1>我的订单</h1>
</template>
<script>
export default {
  data () {
    return {
      curClass: ''
    }
  },
  route: {
    data (transition) {
      let self = this
      let curTag = transition.to.query.tag
      console.log(transition.to.query.tag)
      if (curTag !== 'all') {
        self.curClass = 'tag' + curTag
      }
    }
  },
  components: {
    'headBar': require('../components/header.vue')
  },
  methods: {
    orderTab (n) {
      let self = this
      self.curClass = 'tag' + n
    }
  }
}

</script>
<style lang="stylus">
@import '../assets/styl/index'
border-radius()
  border-radius arguments
.order-bar
  background-color: #fff
  border-bottom: 1px solid border-color
  text-align center
  .flex
    padding:px2rem(20px) 0
.section-order-title
	position relative
	&.cur1 span.order-cur
		transform: translateX(0%)
		background-color red
	&.cur2 span.order-cur
		transform: translateX(100%)
		background-color red
	&.cur3 span.order-cur
		transform: translateX(200%)
		background-color red
	&.cur4 span.order-cur
		transform: translateX(300%)
		background-color red
.order-cur
	width 25%
	height px2rem(4)
	bottom 0
	z-index 10
	position absolute
	transition all 0.2s linear
</style>



